import { Component, EventEmitter, Input, OnInit, Output, } from '@angular/core';
import { Observable } from 'rxjs';
import { hotels$ } from '../data';
import 'rxjs';

@Component({
	selector: 'course-selector',
	templateUrl: './selector.component.html',
	styleUrls: ['./selector.component.css']
})
export class SelectorComponent implements OnInit {
	public hotels$: Observable<hotel[]> = hotels$;

	public hotels: hotel[];

	@Input()
	public selectedHotels: hotel[];

	@Output()
	public selectHotel: EventEmitter<hotel[]> = new EventEmitter();

	public constructor() { }

	public ngOnInit(): void {
		hotels$.subscribe(
			(currentHotels: hotel[]) => {
				this.hotels = currentHotels;
				this.selectHotels(this.hotels);
			}
		);
	}

	public selectHotels(hotels: hotel[]): void {
		this.selectHotel.emit(hotels);
	}
}



