import { Component, OnInit, ViewEncapsulation, } from '@angular/core';
import { Observable } from 'rxjs';
import { hotels$ } from './data';
import 'rxjs';

@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['../assets/css/style.css', './app.component.css', ],
	encapsulation: ViewEncapsulation.None
})
export class AppComponent implements OnInit {
	public title: string = 'Hot Weather Widget';

	public hotels$: Observable<hotel[]> = hotels$;

	public currentHotels: hotel[];

	public ngOnInit(): void {
	}

	public setHotel(hotels: hotel[]): void {
		this.currentHotels = hotels;
	}
}



